@import "../variables";

.normal-font-size{
    font-size: $font-size-normal;
    line-height: 1.6;
    font-weight: 400;

    @include for-tablet-portrait-up{
        font-size: 14px;
    }

    @include for-tablet-landscape-up{
        font-size: 16px;
    }

    @include for-desktop-up{
        font-size: 16px;
    }
}

body{
    font-family:  Raleway, 'Open Sans', sans-serif;

    @extend .normal-font-size;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
    font-family: "Oxygen", Arial, sans-serif;
    margin-top: 0;
    margin-bottom: .75em;
    font-weight: 300; 
}
.h1 { font-size: 2.75em; }
.h2 { font-size: 2.00em; }
.h3 { font-size: 1.75em; }
.h4 { font-size: 1.5em; }
.h5 { font-size: 1.25em; }
.h6 { font-size: 1em; }
 
h1 { @extend .h1; }
h2 { @extend .h2; }
h3 { @extend .h3; }
h4 { @extend .h4; }
h5 { @extend .h5; }
h6 { @extend .h6; }

.dm-blog-text{
    h1{ font-size: 1.75em; }
    h2{ font-size: 1.25em; }
    h3{ font-size: 1.15em; }
    h4{ font-size: 1em; }
    h5{ font-size: 1em; }
    h6{ font-size: 1em; }    
}
